$imagesPath: '../../assets';

@keyframes full-rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.station-item {
  background-color: #202020;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 10px;
  min-height: 120px;
  padding: 10px 15px 26px;
  position: relative;
  transition: all 0.3s cubic-bezier(0.39, 0.58, 0.57, 1);
  user-select: none;
  @include breakpoint(smm) {
    height: 120px;
    padding: 20px 15px;
  }
  @include breakpoint(sm) {
    height: 260px;
    max-width: 260px;
    margin-bottom: 22px;
    margin-left: 22px;
    padding: 35px;
    position: relative;
    width: 100%;
  }
  @include breakpoint(md) {
    max-width: 300px;
    height: 300px;
    width: 100%;
    margin-bottom: 18px;
    margin-left: 18px;
    padding: 30px;
  }
  @include breakpoint(lg) {
    max-width: 410px;
    height: 410px;
    padding: 40px;
    width: 100%;
  }
  @include hovering {
    &:hover {
      .station-item {
        &__image {
          filter: grayscale(0);
        }

        &__name {
          color: $gold;
        }

        &__description {
          color: $white;
        }
      }
    }
  }
  @include breakpoint(xl) {
    max-width: 500px;
    height: 500px;
    padding: 50px;
    width: 100%;
  }

  &__container {
    display: flex;
    @include breakpoint(sm) {
      display: block;
    }
  }

  &__contains {
    padding-left: 15px;
    padding-right: 0;
    @include breakpoint(sm) {
      padding-left: 0;
      padding-right: 0;
      width: 100%;
    }
    @include breakpoint(sm) {
      margin-top: 12px;
    }
    @include breakpoint(lg) {
      margin-top: 90px;
    }
    @include breakpoint(xl) {
      margin-top: -35px;
    }
  }

  &__name {
    color: $gray;
    display: flex;
    justify-content: space-between;
    line-height: 1.1;
    margin-bottom: 10px;
    transition: all 0.3s cubic-bezier(0.39, 0.58, 0.57, 1);
    word-break: break-all;
    @include breakpoint(smm) {
      font-size: 14px;
      margin-bottom: 5px;
      margin-top: 3px;
    }
    @include breakpoint(sm) {
      font-size: 12px;
      margin-bottom: 5px;
    }
    @include breakpoint(md) {
      font-size: 16px;
    }
    @include breakpoint(lg) {
      margin-top: 70px;
    }
    @include breakpoint(xl) {
      font-size: 25px;
    }
  }

  &__description {
    color: $gray-dark;
    display: block;
    font-size: 10px;
    transition: all 0.3s cubic-bezier(0.39, 0.58, 0.57, 1);
    word-break: break-word;

    @include breakpoint(smm) {
      font-size: 9px;
      margin-bottom: 20px;
    }
    @include breakpoint(sm) {
      font-size: 12px;
    }
    @include breakpoint(md) {
      font-size: 14px;
    }
    @include breakpoint(xl) {
      font-size: 18px;
    }
  }

  &__figure {
    align-items: stretch;
    justify-content: stretch;
    max-width: 100px;
    min-width: 100px;
    position: relative;
    transition: all 0.3s cubic-bezier(0.39, 0.58, 0.57, 1);
    @include breakpoint(sm) {
      height: 135px;
      max-width: 100%;
      width: 135px;
    }
    @include breakpoint(md) {
      height: 160px;
      width: 160px;
    }
    @include breakpoint(xl) {
      height: 300px;
      width: 300px;
    }

    &::after {
      @include breakpoint(sm) {
        animation: full-rotate 5s linear infinite;
        background-image: url('#{$imagesPath}/stations/station-active.svg');
        background-repeat: no-repeat;
        background-size: cover;
        bottom: 0;
        content: '';
        height: 155px;
        opacity: 0;
        position: absolute;
        right: 0;
        top: 0;
        transition: all 0.3s cubic-bezier(0.39, 0.58, 0.57, 1);
        width: 155px;
      }
      @include breakpoint(sm) {
        height: 135px;
        width: 135px;
      }
      @include breakpoint(md) {
        height: 160px;
        width: 160px;
      }
      @include breakpoint(xl) {
        height: 300px;
        width: 300px;
      }
    }
  }

  &__image {
    filter: grayscale(1);
    object-fit: cover;
    position: relative;
    transition: all 0.3s cubic-bezier(0.39, 0.58, 0.57, 1);
    width: 100%;
    z-index: 1;
    @include breakpoint(smm) {
      max-width: 75px;
      width: 75px;
      height: 75px;
    }
    @include breakpoint(sm) {
      max-width: 135px;
      width: 135px;
      height: 135px;
    }
    @include breakpoint(md) {
      max-width: 160px;
      width: 160px;
      height: 160px;
    }
    @include breakpoint(lg) {
      max-width: 230px;
      width: 230px;
      height: 230px;
    }
    @include breakpoint(xl) {
      max-width: 300px;
      height: 300px;
      width: 300px;
    }
  }

  &__timer {
    margin-top: 9px;
    @include breakpoint(smm) {
      font-size: 10px;
    }
    @include breakpoint(lg) {
      font-size: 12px;
    }
    @include breakpoint(xl) {
      font-size: 16px;
    }
  }

  &__statuses {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-bottom: 5px;
    min-height: 22px;
    padding-top: 0;

    @include breakpoint(smm) {
      position: absolute;
      right: 33px;
      top: 85px;
    }
    @include breakpoint(sm) {
      position: absolute;
      right: 10px;
      top: 23px;
    }
    @include breakpoint(md) {
      position: absolute;
      right: 20px;
      top: 30px;
    }
    @include breakpoint(lg) {
      position: absolute;
      right: 20px;
      top: 40px;
    }
    @include breakpoint(xl) {
      position: absolute;
      right: 15px;
      top: 43px;
    }
  }

  &__status {
    margin-left: 12px;

    &.status--more {
      display: none;
      width: 33px;

      .station-item--current & {
        display: flex;
      }
    }
  }

  &--current {
    background: linear-gradient(100.88deg,
            rgba(156, 156, 156, 0.37) -3.1%,
            rgba(20, 20, 20, 0.37) 50.26%),
    rgba(45, 45, 45, 0.37);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  }

  &--current & {
    &__name {
      color: $gold;
    }

    &__description {
      color: $white;
    }

    &__figure {
      @include breakpoint(sm) {
        height: 135px;
        width: 135px;
        @include breakpoint(md) {
          height: 160px;
          width: 160px;
        }
        @include breakpoint(xl) {
          height: 300px;
          width: 300px;
        }
      }

      &::after {
        height: 56px;
        opacity: 1;
        width: 56px;
        @include breakpoint(sm) {
          height: 135px;
          width: 135px;
          right: -27%;
        }
        @include breakpoint(md) {
          height: 160px;
          width: 160px;
          right: -27%;
        }
        @include breakpoint(lg) {
          height: 230px;
          width: 230px;
          right: -83%;
        }
        @include breakpoint(xl) {
          height: 300px;
          width: 300px;
          right: -28%;
        }
      }
    }

    &__contains {
      @include breakpoint(sm) {
        width: 100%;

      }
    }

    &__image {
      filter: grayscale(0);
    }
  }

  &--disable {
    opacity: 0.5;
    pointer-events: none;
  }
}

.status--more {
  @include breakpoint(xl) {
    height: 25px;
    position: absolute;
    right: 15px;
    text-align: center;
    top: 0;
    width: 45px;
  }
}

.wrapper {
  overflow-y: hidden;
}

.station-item__description-block {
  @include breakpoint(sm) {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
}