.station-info {
  position: relative;
  opacity: 0.6;
  height: 100vh;
  overflow: hidden;
  background-color: black;

  @include breakpoint(smm) {
    margin-top: 30px;
  }
  @include breakpoint(md) {
    margin-top: 20px;
  }
  @include breakpoint(lg) {
    margin-top: 0;
    margin-left: 253px;
  }
  @include breakpoint(xl) {
    margin-top: 0;
    margin-left: 379px;
  }

  &__img {
    object-fit: cover;
    height: 100%;
    float: right;
    opacity: 1;
    user-select: none;
  }
}

.station-info__close {
  position: absolute;
  top: 15px;
  right: 15px;
}

.station-info__container {
  position: absolute;
  top: 30px;
  left: 132px;
  z-index: 3;
  @include breakpoint(smm) {
    left: 20px;
  }

  @include breakpoint(ss) {
    left: 50px;
  }
  @include breakpoint(xl) {
    left: 150px;
  }
}

.station {

  background-color: #2a2a2a;
  border-radius: 4px;
  position: relative;
  @include breakpoint(smm) {
    width: 280px;
    height: 360px;
    padding: 20px;
    margin: 0 auto;
  }
  @include breakpoint(ss) {
    width: 500px;
    height: 250px;
    margin: 0;

  }
  @include breakpoint(tablet) {
    width: 650px;
    height: 250px;

  }
  @include breakpoint(sm) {
    width: 830px;
    height: 297px;
  }
  @include breakpoint(lg) {
    width: 830px;
    height: 340px;
  }
  @include breakpoint(xl) {
    width: 1000px;
    height: 470px;
    padding: 40px;
  }
}

.description {
  &-title {
    color: #F4F7FF;
    margin-bottom: 32px;
    @include breakpoint(smm) {
      font-size: 20px;
      margin-bottom: 10px;
    }
    @include breakpoint(ss) {
      font-size: 34px;
      margin-bottom: 10px;
    }
    @include breakpoint(lg) {
      font-size: 27px;
      margin-bottom: 32px;
    }
    @include breakpoint(xl) {
      font-size: 60px;
    }
  }

  &-text {
    color: #A9ACB6;
    max-width: 530px;
    margin-left: 10px;
    padding: 15px;
    @include breakpoint(smm) {
      font-size: 12px;
      margin-bottom: 30px;
      margin-right: 10px;
    }
    @include breakpoint(ss) {
      margin-bottom: 90px;
      margin-right: 10px;
      font-size: 16px;
    }
    @include breakpoint(lg) {
      margin-bottom: 103px;
    }
    @include breakpoint(xl) {
      font-size: 40px;
      max-width: 1000px;
      margin-bottom: 100px;
    }
  }
}


.station_title {
  text-align: center;
  color: white;
  font-weight: 500;
  margin-bottom: 20px;
  @include breakpoint(sm) {
    font-size: 16px;
  }
  @include breakpoint(ss) {
    font-size: 20px;
    margin-bottom: 30px;
  }
  @include breakpoint(xl) {
    font-size: 40px;
  }
}

.station-inner {
  @include breakpoint(smm) {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  @include breakpoint(ss) {
    display: flex;
    flex-direction: row;
    justify-content: left;
    text-align: left;
  }

  &__img {
    width: 150px;
    height: 150px;
    position: relative;
    z-index: 10;
    @include breakpoint(smm) {
      margin-right: 0;
      margin-bottom: 23px;
    }
    @include breakpoint(ss) {
      margin-right: 80px;
    }
    @include breakpoint(tablet) {
      margin-right: 40px;
    }
    @include breakpoint(lg) {
      width: 200px;
      height: 200px;
    }
    @include breakpoint(xl) {
      width: 300px;
      height: 300px;
    }

  }

  &__description {
    display: flex;
    flex-direction: column;
    @include breakpoint(xl) {
      font-size: 40px;
    }
  }

  &__name {
    margin-bottom: 15px;
    font-size: 16px;
  }

  &__link {
    text-decoration: none;
    color: white;
    font-size: 12px;
    height: 26px;
    border: 1px solid white;
    border-radius: 5px;
    @include breakpoint(smm) {
      width: 100px;
      padding: 5px 0 23px 0;
    }
    @include breakpoint(ss) {
      width: 95px;
      padding: 6px 12px 8px 12px;
    }
    @include breakpoint(md) {
      margin-top: 36px
    }
    @include breakpoint(xl) {
      font-size: 27px;
      padding: 15px;
    }
    @include hovering {
      &:hover {
        color: $gold;
        border-color: $gold;
      }
    }
  }
}

.station-inner_timer {
  @include breakpoint(smm) {
    margin-top: 20px;
  }
  @include breakpoint(ss) {
    margin-top: 16px;
  }
}

.station-share {
  float: right;
  width: 17px;
  height: 18px;
  cursor: pointer;
}

.station-inner__d {
  margin-top: 16px;
  @include breakpoint(smm) {
    width: 200px;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }
  @include breakpoint(ss) {
    display: block;
  }
}

.image_active {
  width: 44px;
  height: 44px;
  position: absolute;
  cursor: pointer;
  z-index: 15;
  @include breakpoint(smm) {
    top: 32%;
    left: 40%;
  }
  @include breakpoint(ss) {
    top: 53%;
    left: 15%;
  }
  @include breakpoint(tablet) {
    top: 53%;
    left: 12%;
  }
  @include breakpoint(sm) {
    top: 44%;
    left: 9%;
  }
  @include breakpoint(lg) {
    top: 48%;
    left: 12%;
  }
}

.image_disable {
  display: none;
}
